<template>
  <div class="tab-control">
    <div class="tab-control-item" v-for="(item,index) in titles" :class="{active: index === currentIndex}" @click="itemClick(index)" >
      <a src="#">
        <span>{{item.title}}</span>
      </a>
    </div>
  </div>
</template>

<script>
  export default {
    name: "TabControl",
    props:{
      titles:{
        type:Array,
        default:[]
      }
    },
    data(){
      return {
        currentIndex: 0
      }
    },
    methods:{
      itemClick(index){
        this.currentIndex = index;
        //子组件向父组件的通信操作
        console.log("zi"+index);
        let i = index
        this.$emit("itemClick",i)
      }
    }
  }
</script>

<style scoped>
  .tab-control{
    padding:1.2rem ;
  }
  .tab-control-item {
    font-size: 1rem;
    color: #999;
    text-align: center;
    display: inline-block;
    background-color: #F6F6F6;
    padding: .20833rem .41667rem;
    border-radius: 1.5rem;
    margin-right: 0.5rem;
  }
  .active {
    background-color: rgba(61,169,255,.1);
  }
  .active span{
    font-weight: 700;
    color: #3DA9FF;
  }
</style>
