<template>
  <!--<h2>分类</h2>-->
  <!--<TabControl></TabControl>-->
  <div id="category">
    <tab-control :titles="titles" @itemClick="itemClick"></tab-control>
    <content-message :ContMData="messages[this.currentCommentUserId].list"></content-message>
    <load-more @click.native="loadMore"></load-more>
    <advertisement></advertisement>
  </div>
</template>

<script>
  // 外部插件引用区
  // import {ajax} from "../../common/ajax";

  //公共组件区
  import TabControl from "../../components/content/tabControl/TabControl"
  import ContentMessage from "./childCopms/ContentMessage";
  import Advertisement from "../../components/content/advertisement/Advertisement";
  import LoadMore from "../../components/content/loadMore/LoadMore";


  //网络请求组件组
  import {getCategroyMessgaes} from "../../notework/request";


  export default {
    name: "category",
    components:{
      LoadMore,
      Advertisement,
      ContentMessage,
      TabControl
    },
    data(){
      return{
        titles:[{
          "title": "时代周刊",
          "commentUserId": 27081345,
        }, {
          "title": "经济学人",
          "commentUserId": 17756655,
        }, {
          "title": "BBC英语",
          "commentUserId": 26394077,
        }],
        currentCommentUserId:27081345,
        messages:{
          "27081345":{page:0,list:[]},
          "17756655":{page:0,list:[]},
          "26394077":{page:0,list:[]}
        }
      }
    },
    created(){

      this.getCategroyMessgaes(27081345);
      this.getCategroyMessgaes(17756655);
      this.getCategroyMessgaes(26394077)

    },
    // computed:{

    // },
    methods:{
      //时间监听相关方法
      itemClick(index){
        if (index === 0){
          this.currentCommentUserId = 27081345
        }else if (index === 1){
          this.currentCommentUserId = 17756655
        } else if(index === 2){
          this.currentCommentUserId = 26394077
        }

        // let current = this.messages[currentCommentUserId];
        // this.getCategroyMessgaes(current);

      },
      loadMore(){
        let data=this.currentCommentUserId;
        this.getCategroyMessgaes(data);
      },

      //网络请求相关方法
      getCategroyMessgaes(commentUserId){
        let Nsize = (this.messages[commentUserId].page + 1)*8;
        // console.log(Nsize);
        //   /*
        //   箭头函数被绑定到父级上下文，因此其中的this会指向父级组件，
        //   将回调函数中的function改成箭头函数，会将this从匿名对象重新指向外部的vue组件。
        //    */
        getCategroyMessgaes(commentUserId,Nsize,data =>{
          // console.log(11);
          // console.log(data);
          this.messages[commentUserId].list = data;
          // console.log(this.messages[commentUserId].list);
          console.log(this.messages[commentUserId].page);
          this.messages[commentUserId].page +=1;
          console.log(this.messages[commentUserId].page);
          // console.log(this.messages[commentUserId].page);

        })
      }
    }
  }
</script>

<style scoped>
  #category{
    margin-top:44px ;

  }
  .content-message{
    min-height: 530px;
    /*background-color: #99ffa0;*/
  }

</style>
